import React from "react"
import {rhythm} from "../utils/typography"
import FaFacebookOfficial from "react-icons/lib/fa/facebook-official"
import FaTwitterSquare from "react-icons/lib/fa/twitter-square"
import SocialLink from "./sociallink"
import presets from "../utils/presets"
import Navbar from "./navbar"
import NavItem from "./navitem"
import Link from "gatsby-link"

const Header = ({children}) => (
  <div style={{
    display: 'flex',
    width: `100%`,
    position: `fixed`,
    paddingTop: rhythm(0.5),
    zIndex: 10,
    backgroundColor: presets.headerBackgroundColor,
    boxShadow: `0.3 0.3 ${presets.shadowColor}`
  }} >
    <Link to="/" style={{
      textDecoration:'none',
      flex: `0 1 ${rhythm(6)}`,
      marginLeft: presets.contentSideMargin
    }}>
      {children}
    </Link>
    <div >
      <Navbar>
        <NavItem Link="catalog">Catalog</NavItem>
        <NavItem Link="contact">Contact us</NavItem>
      </Navbar>
    </div>
    <div style={{
      marginLeft: 'auto',
      lineHeight: `${rhythm(2)}`,
      marginRight: presets.contentSideMargin
    }} >
      <SocialLink to="https://www.facebook.com/Venovel-337315656737567">
        <FaFacebookOfficial style={{ verticalAlign: `text-top` }}/>
      </SocialLink>
      <SocialLink to="#">
        <FaTwitterSquare style={{ verticalAlign: `text-top` }}/>
      </SocialLink>
    </div>
  </div>
);

export default Header;



