import React from "react"
import Link from "gatsby-link"
import presets from "../utils/presets"
import {rhythm} from "../utils/typography"

const NavItem = (props) => (
  <div style={{
    textTransform: `uppercase`,
    margin: `0 ${rhythm(0.5)}`
  }} >
    <Link
      to={props.Link}
      style={{
        color: presets.brandColor,
        textDecoration: 'None'
      }}
    >
      {props.children}
    </Link>
  </div>
)

export default NavItem;



