import React from "react"
import {rhythm} from "../utils/typography"
import presets from "../utils/presets"

const Navbar = ({children}) =>(
  <div css={{
    display: 'none',
    padding: `0rem 0rem 0rem ${rhythm(2)}`,
    lineHeight: `${rhythm(2)}`,
    [presets.Tablet]: {
        display: `flex`,
    },
  }} >
    {children}
  </div>
)

export default Navbar;



