import React from "react"
import Link from "gatsby-link"
import presets from "../utils/presets"
import MdHome from "react-icons/lib/md/home"
import MdMessage from "react-icons/lib/md/message"
import MdList from "react-icons/lib/md/list"
import {scale} from "../utils/typography"

const MobileNavItems = (props) => (
  <div css={{
    flex: 1
  }} >
    <Link
      style={{
        color: presets.brandColor,
        textDecoration: 'None',
        display: 'flex',
        flexDirection: 'column',
        margin: `0 auto`,
        alignItems: `center`,
        fontSize: scale(-1 / 2).fontSize,
      }}
      to={props.Link} >
      {props.children}
    </Link>
  </div>
)

const NavbarMobile = () => (
  <div css={{
    display: `flex`,
    bottom: 0,
    left: 0,
    right: 0,
    zIndex: 1,
    position: 'fixed',
    textTransform: `uppercase`,
    alignItems: `center`,
    borderTop: `1px solid ${presets.brandColor}`,
    background: `${presets.secondaryColor}`,
    [presets.Tablet]: {
        display: `none`,
    },
  }} className="NavbarMobile" >
    <MobileNavItems Link="/">
      <MdHome style={{
          margin: '0 auto',
          width: '100%',
          height: 'auto',
          maxHeight: '2rem'
      }}/>
      Home
    </MobileNavItems>
    <MobileNavItems Link="catalog">
      <MdList style={{
          margin: '0 auto',
          width: '100%',
          height: 'auto',
          maxHeight: '2rem'
      }}/>
      Catalog
    </MobileNavItems>
    <MobileNavItems Link="contact">
      <MdMessage style={{
          margin: '0 auto',
          width: '100%',
          height: 'auto',
          maxHeight: '2rem'
        }}/>
        Contact us
      </MobileNavItems>
    </div>
)

export default NavbarMobile;



